"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Textarea } from "@/components/ui/textarea"
import { Label } from "@/components/ui/label"
import { Input } from "@/components/ui/input"

interface SymptomsFormProps {
  onBack?: () => void
}

export function SymptomsForm({ onBack }: SymptomsFormProps) {
  const [isRecording, setIsRecording] = useState(false)
  const [symptoms, setSymptoms] = useState("")
  const [step, setStep] = useState(1)

  const handleStartRecording = () => {
    setIsRecording(true)
    // TODO: Implement voice recording
  }

  const handleStopRecording = () => {
    setIsRecording(false)
    // TODO: Stop recording and process voice
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (step === 1) {
      setStep(2)
    } else {
      // TODO: Submit to backend
    }
  }

  return (
    <Card className="max-w-2xl mx-auto bg-white shadow-xl rounded-xl overflow-hidden">
      <div className="bg-blue-600 px-6 py-4">
        <div className="flex items-center justify-between">
          <h2 className="text-white font-semibold">
            {step === 1 ? "Describe tus síntomas" : "Información adicional"}
          </h2>
          <span className="text-blue-100">
            Paso {step} de 2
          </span>
        </div>
      </div>

      <form onSubmit={handleSubmit} className="p-6 space-y-6">
        {step === 1 ? (
          <>
            <div className="space-y-2">
              <Label htmlFor="symptoms" className="text-lg font-semibold text-gray-700">
                ¿Qué síntomas presentas?
              </Label>
              <div className="relative">
                <Textarea
                  id="symptoms"
                  placeholder="Describe tus síntomas con el mayor detalle posible..."
                  value={symptoms}
                  onChange={(e) => setSymptoms(e.target.value)}
                  className="h-32 resize-none text-gray-700 border-gray-200 focus:border-blue-500 focus:ring-blue-500"
                />
                <Button
                  type="button"
                  variant={isRecording ? "destructive" : "outline"}
                  onClick={isRecording ? handleStopRecording : handleStartRecording}
                  className={`absolute bottom-2 right-2 ${isRecording ? 'animate-pulse' : ''}`}
                  size="sm"
                >
                  <svg className="h-5 w-5 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 11a7 7 0 01-7 7m0 0a7 7 0 01-7-7m7 7v4m0 0H8m4 0h4m-4-8a3 3 0 01-3-3V5a3 3 0 116 0v6a3 3 0 01-3 3z" />
                  </svg>
                  {isRecording ? "Detener" : "Dictar"}
                </Button>
              </div>
              <p className="text-sm text-gray-500 mt-2">
                Puedes escribir o usar el micrófono para describir tus síntomas
              </p>
            </div>

            <div className="flex justify-end space-x-4">
              {onBack && (
                <Button type="button" variant="outline" onClick={onBack}>
                  Volver
                </Button>
              )}
              <Button 
                type="submit" 
                className="bg-blue-600 hover:bg-blue-700 text-white"
                disabled={!symptoms.trim()}
              >
                Continuar
              </Button>
            </div>
          </>
        ) : (
          <>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-2">
                <Label htmlFor="age" className="font-medium text-gray-700">
                  Edad
                </Label>
                <Input 
                  id="age" 
                  type="number" 
                  placeholder="Ej: 35" 
                  className="border-gray-200 focus:border-blue-500 focus:ring-blue-500"
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="gender" className="font-medium text-gray-700">
                  Género
                </Label>
                <select
                  id="gender"
                  className="w-full rounded-md border-gray-200 focus:border-blue-500 focus:ring-blue-500"
                >
                  <option value="">Seleccionar</option>
                  <option value="M">Masculino</option>
                  <option value="F">Femenino</option>
                  <option value="O">Otro</option>
                </select>
              </div>
              <div className="space-y-2">
                <Label htmlFor="height" className="font-medium text-gray-700">
                  Altura (cm)
                </Label>
                <Input 
                  id="height" 
                  type="number" 
                  placeholder="Ej: 170" 
                  className="border-gray-200 focus:border-blue-500 focus:ring-blue-500"
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="weight" className="font-medium text-gray-700">
                  Peso (kg)
                </Label>
                <Input 
                  id="weight" 
                  type="number" 
                  placeholder="Ej: 70" 
                  className="border-gray-200 focus:border-blue-500 focus:ring-blue-500"
                />
              </div>
            </div>

            <div className="flex justify-end space-x-4">
              <Button 
                type="button" 
                variant="outline" 
                onClick={() => setStep(1)}
              >
                Atrás
              </Button>
              <Button 
                type="submit" 
                className="bg-blue-600 hover:bg-blue-700 text-white"
              >
                Evaluar síntomas
              </Button>
            </div>
          </>
        )}
      </form>
    </Card>
  )
}
